.navbar {
  position: sticky;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 100;
  background-color: white;
}
.main_container {
  position: relative;
  padding: 3vh 9.5vw 0 9vw;
  background-color: #f5f5f5;
  height: auto;
  padding-bottom: 2vh;
}
.imc_main {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
  height: 100%;
}
.imc_main > div:nth-child(1) {
  width: 25%;
}
.imc_main > div:nth-child(2) {
  width: calc(75% - 1rem);
}
.isb_container {
  margin-top: -0.68px;
  /* max-width: 264px; */
  /* max-height: 9.25rem; */
  height: 100%;
  width: 100%;
  position: relative;
}
.isb_img {
  width: 100%;
  height: 100%;
}
.isb_sub_container {
  position: absolute;
  height: calc(100% - 1.9rem);
  top: 0.3rem;
  right: 0.7rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  gap: 1rem;
}
.isb_sub_container > img {
  height: 36px;
  width: 36px;
  cursor: pointer;
}
.isc_container {
  width: 100%;
  max-height: 4.5rem;
  padding: 19.5px;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  border-bottom: 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--tab-active-color);
}
.isc_sub_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 3px;
}
.isc_sub_container > h6 {
  font-weight: var(--font-weight-600);
  font-size: var(--font-size-20);
  line-height: 23px;
  color: var(--tab-color);
  margin: 0;
}
.isc_sub_container > h6 {
  font-weight: var(--font-weight-600);
  font-size: var(--font-size-14);
  line-height: 16px;
  color: var(--tab-color);
  margin: 0;
}
.isc_sub_container > p {
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-10);
  line-height: 12px;
  color: rgba(18, 18, 18, 0.6);
  margin: 0;
}
/* // for academic department container  */
.iadc_side {
  width: 26%;
}
.iadc_main {
  width: calc(76% - 1rem);
}
/* // for academic department tabs  */
.iadt_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.4%;
}
.iadtc_card {
  flex: 1 1 32.5%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--tab-bg-color);
  color: var(--tab-color);
  padding: 1.2rem;
  transition: all cubic-bezier(0.4, 0, 1, 1) 200ms 50ms;
}
.iadtc_card > h6 {
  margin: 0;
}
.iadtc_card_active,
.iadtc_card:hover {
  background-color: var(--tab-active-bg-color);
  color: var(--tab-active-color);
}
/* // for academic department sidebar  */

.iads_cover {
  width: 100%;
  height: auto;
}
.iads_card {
  width: 100%;
  border-bottom: 0.4px solid rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.4rem 1.3rem;
  transition: all cubic-bezier(0.4, 0, 1, 1) 200ms 50ms;
}
.iads_card:hover,
.iads_card_active {
  background-color: var(--tab-bg-color);
}
.iadsc_inner > h6 {
  font-weight: var(--font-600);
  font-size: var(--font-14);
  line-height: 16px;
  color: var(--tab-color);
  margin: 0;
  margin-bottom: 0.25rem;
}

.iads_card > h6 {
  font-weight: var(--font-600);
  font-size: var(--font-20);
  line-height: 16px;
  color: var(--tab-color);
  margin: 0;
}
.iadsc_inner > p {
  font-weight: var(--font-400);
  font-size: var(--font-10);
  line-height: 12px;
  color: rgba(18, 18, 18, 0.6);
  margin: 0;
}
.iadds_continer {
  position: relative;
}
.iaddsc_globe {
  width: 2.1rem;
  height: 2.1rem;
  position: absolute;
  top: 10%;
  right: 5%;
  cursor: pointer;
}
.iaddsc_edit {
  width: 2.1rem;
  height: 2.1rem;
  position: absolute;
  bottom: 20%;
  right: 5%;
  cursor: pointer;
}
